#note-checkbox{
    display: flex;
    gap: 1rem;
    align-items: center;
}

#job-profile {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: .5rem .75rem;
}


#job-profile h2,
#job-profile article:nth-child(2) {
    grid-column: 1 / -1;
}

#job-profile article:nth-child(5) p {
    text-align: center;
}

.job-detail {
    display: flex;
    flex-direction: column;
}

.job-buttons {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
}

.note-form {
    display: flex;
    flex-direction: column;
    gap: .5rem;
}

.note-form textarea {
    resize: none;
    height: 4rem;
}

/* section | note form */
#notes {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.note {
    display: grid;
    gap: .25rem .5rem;
    grid-template-columns: 1fr auto;
    grid-template-areas:
        "a c"
        "b c";
}

.note h3 {
    grid-area: a;
}

.note p {
    grid-area: b;
    font-variant: normal;
}

.note button {
    grid-area: c;
    height: fit-content;
}

@media screen and (min-width: 768px) {
    #job-profile {
        grid-template-columns: 2fr repeat(4, 1fr);
        height: 100%;
    }

    #job-profile article:nth-child(2) {
        grid-column: 1;
    }

    #job-profile article:nth-child(5) p {
        text-align: inherit;
    }

    #job-options {
        display: flex;
        gap: .5rem 1rem;
    }

    #job-options h2 {
        grid-area: a;
    }

    #job-options .job-buttons {
        grid-area: b;
    }

    #job-options .note-form {
        grid-area: c;
    }

}

@media screen and (min-width: 1024px) {
    .aside button {
        padding: .15rem 1rem;
    }
}